@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap);

body{
  background: url(/portfolio/static/media/background.006fb6bd.png);
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
*{
  outline: none;
}
.home{
  color: black;
  margin-top: -20vh;
  margin-left: 9.5vw;
  font-size: 1.45rem;
  font-weight: bold;
}
#imgHome{
  width: 300px;
  height: 350px;
  border-radius: 50%;
  display: flex;
  margin-left: 68vw;
  margin-top: 20vh;
  justify-content: right;
  box-shadow: 0 0 10px 2px;
}
@media (min-width: 600px){
  #imgHome:hover{
    width: 350px;
    height: 430px;
    transition: all 2s;
  }
}
#imgHome:not(hover){
  transition: all 2s;
}
.designation{
  margin-top: -15PX;
  font-weight: bold;
}
.designation:hover{
  transition:1s;
  letter-spacing: 1rem;
}
.name{
  margin-top: -15PX;
  font-weight: bold;
}
.name:hover{
  transition:1s;
  letter-spacing: 1rem;
}
.hi{
  font-weight: bold;
}
.hi:hover{

  transition:1s;
  letter-spacing: 1rem;
}
.front-end{
  font-family: 'Roboto', sans-serif;
  margin-top: -10px;
  letter-spacing: 0.3rem;
  
}
.front-end:hover{
  
  transition:1s;
  letter-spacing: 0.8rem;
}

*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

nav{
  background:inherit;
  width: 100%;
}
label.logo{
  color: white;
  font-size: 35px;
  line-height: 80px;
  padding: 0 100px;
  font-weight: bold;
}
nav ul{
  float: right;
  margin-right: 20px;
}
nav ul li{
  display: inline-block;
  line-height: 80px;
  margin: 0 10px;
}
nav ul li .a{
  color: white;
  font-size: 17px;
  padding: 7px 13px;
  border-radius: 3px;
  text-transform: uppercase;
  text-decoration: none;
}


.checkbtn{
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}
#check{
  display: none;
}
@media (max-width: 952px){
  label.logo{
    font-size: 30px;
    padding-left: 50px;
  }
  nav ul li .a{
    font-size: 17px;
  }
}
@media (max-width: 890px){
  .checkbtn{
    display: block;
  }
  ul{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #2c3e50 center cover;
    top: 80px;
    left: -100%;
    text-align: center;
    z-index: 50px;
    transition: all .5s;
  }
  nav ul li{
    color: white;
    margin: 17px 0px;
    line-height: 20px;
    
  }
  nav ul li .a{
    font-size: 13.7px;
    margin-left: 1px;
    padding: 4px 7px;
    background: black;
  }
  
  #check:checked ~ ul{
    left: 0;
  }
  html,body{
    background-repeat: repeat;
    background-size: 400vh;
    height: 100%;
  }
  #imgHome{
    width: 190px;
    height: 250px;
    border-radius: 50%;

    
  }
  #img{
    width: 190px;
    height: 100px;
  }
  #img:hover{
    width: 200px;
    height: 230px;
    transition: all 2s;
  }
  #imgHome:hover{
    width: 220px;
    height: 320px;
    transition: all 2s;
  }
  .bodyForm{
    margin-top: 10vh;
    
  }
  .Wrapper{
    top: 50%;
    width: 80vw;
  }
}
@media (max-width: 600px){
  #imgHome{
    width: 180px;
    height: 220px;
    border-radius: 50%;
    margin-left: 20vh;
  }
  #imgHome:hover{
    width: 200px;
    height: 250px;
  }
  .home{
    margin-top: 8vh;
    margin-left: 9.5vw;
    font-size: 1.45rem;
  }
 
  .designation{
    font-size: 25px;
    font-weight: bold;
  }
  .name{
    font-size: 25px;
    font-weight: bold;
  }
  .hi{
    font-size: 25px;
  }
  .front-end{
    font-family: 'Roboto', sans-serif;
    margin-top: -10px;
    font-size: 12px;
    letter-spacing: 0.3rem;
  }

}
#img{
  width: 250px;
  height: 300px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  margin: 70px auto;
  box-shadow: 0 0 10px 2px;
}
#img:hover{
  width: 300px;
  height: 370px;
  transition: all 2s;
}
#img:not(hover){
  transition: all 2s;
}
@media (max-width: 600px){
  #img{
    width:180px;
    height: 220px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    margin: 70px auto;
    box-shadow: 0 0 10px 2px;
  }
  #img:hover{
    width: 220px;
    height: 260px;
    transition: all 2s;
  }
}
.nameAbout{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: -55px;
  font-weight: bold;

}
.desig{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: -15px;
  font-weight: bold;
}
.para{
  margin-top: 25px;
  margin-left: 20px;
  margin-right: 20px;
  text-indent: 50px;
  font-size: 17px;
  font-weight: 600;
}
.para2{
  text-indent: 50px;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 17px;
  font-weight: 600;
}
.skills{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30vh;
  font-weight: bold;
}
.lists{
  margin-left: 45vw;
  margin-top: 23px;
  font-size: 17px;
}
#nameid{
  display: inline;
}
.bodyForm{
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}
.Wrapper{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  padding: 0px 20px;
  margin-top: 45vh;
}
.contact-form{
  max-width: 550px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.7);
  padding: 30px;
  border-radius: 5px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.53);
}
.input-fields{
  display: flex;
  flex-direction: column;
  margin-right: 4%;
}
.input-fields,.msg{
  width: 48%
}
.input-fields .input, .msg textarea{
  margin: 10px 0;
  background: transparent;
  border: 0;
  border-bottom: 2px solid #c5ecfd;
  padding: 10px;
  color: #c5ecfd;
  width: 100%;
}
.msg textarea{
  height: 166px;
}

::-webkit-input-placeholder{
  color: #c5ecfd;
}
.btn{
  background: #39b7dd;
  text-align: center;
  padding-left: 40%;
  padding-right: 40%;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  outline: none;
  border: 0;
}
.emailMe{
  color: #39b7dd;
}


@media screen and (max-width:600px){
  .contact-form{
    flex-direction: column;
    width: 300px;
    height: 400px;
  }
  .msg textarea{
    height: 50px;
  }
  .input-fields,.msg{
    width: 100%
  }
  .btn{
    padding-left: 41%;
    padding-right: 45.5%;
  }
}

.call{
  margin: 20px;
  text-align: center;
  justify-content: center;
  font-size: 17px;
  
}
#or{
  font-size: 19px;
}
@media (min-width: 900px){

  .Projects{
    width: 80vw;
    height: 70vh;
    background: inherit;
    margin: 90px auto;
    position: relative;
  }
  .projectimg1, .projectimg2, .projectimg3, .projectimg4,.projectimg5,.projectimg6{
    width: 350px;
    height: 200px;
    border-radius: 35px;
    box-shadow: 0 0 3px 0.9px;
  
  }
  .projectimg1:hover,.projectimg2:hover, .projectimg3:hover, .projectimg4:hover,.projectimg5:hover,.projectimg6:hover{
    width: 370px;
    height: 220px;
    transition: all 0.7s;
  }
  .projectimg1:not( :hover ),.projectimg2:not( :hover ),.projectimg3:not( :hover ),.projectimg4:not( :hover ),.projectimg5:not( :hover ),.projectimg6:not( :hover ){ 
    transition: all 0.7s;
   }
  
  .project1{
    position: absolute;
    margin: 15px;
  }
  .project2{
    position: absolute;
    left: 428px;
    margin: 15px;
  }
  .project3{
    position: absolute;
    left: 850px;
    margin: 15px;
  }
  .project4{
    position: absolute;
    top: 50%;
    margin: 15px;
  }
  .project5{
    position: absolute;
    top: 50%;
    margin: 15px;
    left: 428px;
  }
  .project6{
    position: absolute;
    left: 69%;
    top: 52%;
    left: 870px;
  }
  .projectName{
    margin-top: 10px;
  }
}


@media (max-width: 900px){
  body{
    background: cover;
    height: 200vh;
  }
  .projectimg1, .projectimg2, .projectimg3, .projectimg4,.projectimg5,.projectimg6{
    width: 60vw;
    height: 20vh;
    border-radius: 35px;
    margin: 20px 80px;
    box-shadow: 0 0 3px 0.9px;
  
  }
  
  .project1{

  margin-top: 50px;
  }
  .projectName{
    margin-top: -13px;
  }
}
